
<template>
<div class="mycarport">
    <el-table :data="listData" size="small" border  style="margin-top:20px;width:100%;"
    :default-sort = "{prop: 'date', order: 'descending'}">
        <el-table-column align="center" prop="memName" label="订单号" sortable ></el-table-column>
        <el-table-column align="center" prop="type" label="车位编号" sortable></el-table-column>
        <el-table-column align="center" prop="type" label="所属小区" sortable></el-table-column>
        <el-table-column align="center" prop="uaaId" label="车位价格" sortable></el-table-column>
        <el-table-column align="center" prop="uaaId" label="收益利率" sortable></el-table-column>
        <el-table-column align="center" prop="id" label="使用权益" sortable></el-table-column>
        <el-table-column align="center" prop="uaaId" label="有效期" sortable></el-table-column>
        <el-table-column align="center" label="操作" width="450" fixed="right">
            <template slot-scope="scope">
                <!-- <el-button type="primary" size="mini" @click="detailsopen(scope.row)"></el-button> -->
                <el-button type="info" size="mini" @click="carportEdit(scope)">详情</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">转让</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">打款赎单</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">质押融资</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">申请回购</el-button>
            </template>
        </el-table-column>
    </el-table>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.size" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'MyCarport',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                mobilephone: '',
                memName: '',
                uaaId: '',
                memSource: '',
                page: 1,
                size: 10,
                total: 1,
                mLevel: '',
                type:''
            },
            listData: [],
        };
    },
    methods: {
        onLoad() {
            this.listData = [
                {
                memName: '2016-05-02',
                uaaId: '1518',
                id:'1'
                }, {
                memName: '2016-05-04',
                uaaId: '1517'
                }, {
                memName: '2016-05-01',
                uaaId: '1519'
                }, {
                memName: '2016-05-03',
                uaaId: '1516'
                }
            ]
            // getMemberList(this.listQuery).then(res => {
            //     this.listData = res.data
            //     this.listQuery.total = parseInt(res.headers['x-total-count'])
            //     this.loading = false
            // })
        },
        setPage(val) {
            this.listQuery.page = val
            this.onLoad()
        },
        setSize(val) {
            this.listQuery.size = val
            this.onLoad()
        },
        detailsopen(){

        },
        add(){

        },
        formatter(row, column) {
            return row.address;
        },
        carportEdit(row){
            console.log(row.row.id)
             this.$router.push({
                path: '/home',
                query: {
                    id: row.row.id
                }
            })
        }
    },
    created() {
        this.onLoad()
    }
  }
</script>
<style scoped>
.mycarport .pagination-container{
    margin-top:20px;
    text-align: center;
}
</style>