
<template>
<div class="unpaid">
    
    <el-table :data="listData" size="small" border  style="margin-top:20px"
    :default-sort = "{prop: 'date', order: 'descending'}">
        <el-table-column align="center" prop="memName" label="订单号" sortable></el-table-column>
        <el-table-column align="center" prop="type" label="金额" sortable></el-table-column>
        <el-table-column align="center" prop="uaaId" label="车位信息" sortable></el-table-column>
        <el-table-column align="center" label="操作" width="320">
            <template slot-scope="scope">
                <!-- <el-button type="primary" size="mini" @click="detailsopen(scope.row)"></el-button> -->
                <el-button type="info" size="mini" @click="add(scope.row.id)">立即支付</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">贷款购买</el-button>
                <el-button type="info" size="mini" @click="add(scope.row.id)">取消订单</el-button>
            </template>
        </el-table-column>
    </el-table>
    <div class="pagination-container">
        <el-pagination background @size-change="setSize" @current-change="setPage" :current-page="listQuery.page" :page-sizes="[10,20,30,50]" :page-size="listQuery.size" layout="total, sizes, prev, pager, next, jumper" :total="listQuery.total">
        </el-pagination>
    </div>
</div>

</template>


<script>
import { pageSet } from '@/utils/mixins.js'
  export default {
    name:'UnPaid',
    mixins: [pageSet],
    data() {
        return {
            listQuery:{
                mobilephone: '',
                memName: '',
                uaaId: '',
                memSource: '',
                page: 1,
                size: 10,
                total: 1,
                mLevel: '',
                type:''
            },
            listData: [],
        };
    },
    methods: {
        onLoad() {
            this.listData = [
                {
                memName: '2016-05-02',
                uaaId: '1518'
                }, {
                memName: '2016-05-04',
                uaaId: '1517'
                }, {
                memName: '2016-05-01',
                uaaId: '1519'
                }, {
                memName: '2016-05-03',
                uaaId: '1516'
                }
            ]
            // getMemberList(this.listQuery).then(res => {
            //     this.listData = res.data
            //     this.listQuery.total = parseInt(res.headers['x-total-count'])
            //     this.loading = false
            // })
        },
        setPage(val) {
            this.listQuery.page = val
            this.onLoad()
        },
        setSize(val) {
            this.listQuery.size = val
            this.onLoad()
        },
        detailsopen(){

        },
        add(){

        },
        formatter(row, column) {
            return row.address;
        }
    },
    created() {
        this.onLoad()
    }
  }
</script>
<style scoped>
.unpaid .pagination-container{
    margin-top:20px;
    text-align:center
}
</style>